@font-face
{
    src:url('PlayfairDisplay-Regular.ttf');
    font-family:Normal;
}

@font-face
{
    src:url('PlayfairDisplay-Black.ttf');
    font-family:Bold;
}
:root
{
    font-size:2.13em;
    font-family:Verdana;
    letter-spacing:0.7px;
}

*
{
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
}

div
{
    padding:0;
    margin:0;
}

svg
{
    display:table;
    margin:auto;
}

a
{
    display:table;
    text-decoration:none;
    color:rgba(0,0,0,0.6);
    cursor:pointer;
}

a:before{
    content:'\2027';
    color:rgba(0,0,0,0.9);
}

input,textarea,select
{
    display:table;
    max-width:100%;
    /* border:none; */
    background-color:rgba(0,0,0,0);
    /* appearance:none; */
    /* -webkit-appearance:none; */
    /* -moz-appearance:none; */
    resize:none;
    padding:0.1rem;
}

html,body
{
    margin:auto;
    width:100%;
    height:100%;
    user-select:none;
    overflow-x:hidden;
    overflow-y:auto;
    -moz-user-select:none;
    -webkit-user-select:none;
    -ms-user-select:none;
    -ms-touch-action:manipulation;
        touch-action:manipulation;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
}

#anchor
{
    width:100%;
    height:100%;
    overflow-y:hidden;
    display:-ms-flexbox;
    display:flex;
}

.global---page-title
{
    font-size:1rem;
    color:rgba(0,0,0,0.7);
}

.global---page-section
{
    font-size:0.6rem;
    padding:0.3rem;
}


